@tailwind base;
@tailwind components;
@tailwind utilities;

[type="text"],
input:where(:not([type])),
[type="email"],
[type="url"],
[type="password"],
[type="number"],
[type="date"],
[type="datetime-local"],
[type="month"],
[type="search"],
[type="tel"],
[type="time"],
[type="week"],
[multiple],
textarea,
select {
appearance:none;
background-color:transparent;
border-color:unset;
border-width:0;
border-radius:unset;
padding:unset;
font-size:unset;
line-height:unset;
}
/*Override default focus colors for tailwindcss-forms https://github.com/tailwindlabs/tailwindcss-forms */
[type="text"]:focus,
[type="email"]:focus,
[type="url"]:focus,
[type="password"]:focus,
[type="number"]:focus,
[type="date"]:focus,
[type="search"]:focus,
[multiple]:focus,
textarea:focus,
select:focus{
   --tw-ring-color:transparent;
border-color:transparent;
}
[type="radio"]:focus,
[type="checkbox"]:focus{
--tw-ring-color:transparent;
}
@layer components{
/*-------------------------------------
ContainerCSS
--------------------------------------*/
.container-xs{
@apply w-full mx-auto max-w-[1384px];
}
}