body{
    margin:0;
    padding:0;
    font-family:Inter;
    }
    *{
    box-sizing:border-box;
    line-height:normal;
    font-family:inherit;
    margin:unset;
    }
    .columntitle_one_border{
    backdrop-filter:blur(1px);
    &::before{
    content:"";
    position:absolute;
    inset:0;
    background:linear-gradient(180deg,#4a5055,#2a2e31);
    background-clip:border-box;
    mask:
    linear-gradient(#fff 0 0)padding-box,
    linear-gradient(#fff 0 0);
    mask-composite:exclude!important;
    z-index:-10;
    border-radius:8px;
    border:1pxsolidtransparent;
    }
    }
    .columns_border{
    backdrop-filter:blur(1px);
    &::before{
    content:"";
    position:absolute;
    inset:0;
    background:linear-gradient(180deg,#484e53,#2f3338);
    background-clip:border-box;
    mask:
    linear-gradient(#fff 0 0)padding-box,
    linear-gradient(#fff 0 0);
    mask-composite:exclude!important;
    z-index:-10;
    border-radius:8px;
    border:1pxsolidtransparent;
    }
}

    input{
    background-color:transparent;
    width:100%;
    }
    input:focus{
    outline:none;
    }
.ReactModal__Overlay{
 display:flex;
  justify-content:center;
  align-items:center;
  }
  a{
  text-decoration:none;
  display:block;
  }
  ::-webkit-scrollbar{
  width:4px;
  height:4px;
  background:transparent;
  }
  ::-webkit-scrollbar-track{
  background:transparent;
  }
  ::-webkit-scrollbar-thumb{
  background:#6b6b6b;
  border-radius:10px;
  }
  .ps-menu-icon{
  width:unset!important;
  min-width:unset!important;
  justify-content:start!important;
  margin:0!important;
  }
  .ps-menu-button,
  .ps-menu-icon{
  height:unset!important;
  }
  .ps-menu-button:hover{
  background-color:unset!important;
  }
  .ps-sidebar-container,
  .ps-submenu-content{
  background-color:transparent!important;
  overflow:unset;
  }
  .ps-collapsed.ps-menu-icon{
  min-width:100%!important;
  justify-content:center!important;
  }
  .ps-collapsed{
  width:80px!important;
  }
  .ps-sidebar-container,
  .ps-menu-root,
  .ps-menu-root>ul{
  width:100%;
  display:flex;
  flex-direction:column;
  }
  .ps-sidebar-root{
  min-width:unset!important;
  }
  .ps-collapsed,
  .ps-collapsed.ps-menu-root,
  .ps-collapsed.ps-menu-button{
    padding-left:0px!important;
    padding-right:0px!important;
    margin-left:unset!important;
    margin-right:unset!important;
    }
    .ps-collapsedul{
    align-items:center;
    }
    .ps-menu-label{
    display:flex;
    justify-content:space-between;
    align-items:center;
    }
    .ps-sidebar-container::-webkit-scrollbar{
    width:2px;
    }
  